.CourseMasterS {
    position: absolute;
    top: 10%;
    left: 23%;
    width: 1050px;
}

.usergrouptableS {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    margin-top: 1px;
    outline: none;
    width: 100%;
    outline: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 0px 0px 6px 6px;
}

.usergrouptableS th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 10px;
    font-family: AvertaStd-Semibold;
    font-size: 16px;
    line-height: 26px;
    text-align: center;
    letter-spacing: 0.02em;
    background: #4285F4;
    color: #fff;
    font-weight: 1;
}

.usergrouptableS td {
    text-align: center;
    padding: 10px;
    border-right: 2px solid rgba(0, 0, 0, 0.4);
    border-bottom: 2px solid rgba(0, 0, 0, 0.4);
    text-align: center;
    font-family: Avertastd-Semibold;
    background: #fff;
    color: #000;
    font-weight: 1;
}






.TableHeadS {
    background: #0759E4;
    margin-top: 40px;
    border-radius: 6px 6px 0px 0px;
    display: flex;
}

.TableHeadS p {
    margin-left: 20px;
    padding: 14px;
    font-family: AvertaStd-Semibold;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.02em;
    color: #FFFFFF;
}

.GroupSelectS {
    margin: 10px 0;
    margin-left: 20rem;
    font-family: AvertaStd-Semibold;
    font-size: 16px;
    line-height: 18px;
    letter-spacing: 0.02em;
    color: rgba(0, 0, 0, 0.6);
    box-shadow: 0px 1px 9px rgba(0, 0, 0, 0.25);
    border-radius: 6px;
    outline: none;
    border: none;
    height: 30px;
    padding-left: 15px;
    width: 140px;
}

.ExactlySelectS {
    margin: 10px 0;
    margin-left: 1rem;
    font-family: AvertaStd-Semibold;
    font-size: 16px;
    line-height: 18px;
    padding-left: 15px;
    outline: none;
    border: none;
    letter-spacing: 0.02em;
    color: rgba(0, 0, 0, 0.6);
    box-shadow: 0px 1px 9px rgba(0, 0, 0, 0.25);
    border-radius: 6px;
    height: 30px;
    width: 110px;
}

.SearchIconS {
    color: rgba(0, 0, 0, 0.6);
    font-size: 18px;
    margin: 10px 0 10px 1rem;
    padding-left: 4px;
    height: 28px;
    border: none;
    outline: none;
    background: #fff;
    border-radius: 6px 0 0 6px;
}

.SearchBoxS {
    margin: 10px 0;
    background: #FFFFFF;
    box-shadow: 0px 1px 9px rgba(0, 0, 0, 0.25);
    border-radius: 0 6px 6px 0px;
    height: 28px;
    outline: none;
    border: none;
    width: 170px;
    font-family: AvertaStd-Semibold;
    font-size: 16px;
    padding-left: 10px;
    line-height: 18px;
    letter-spacing: 0.02em;
    color: rgba(0, 0, 0, 0.6);
}

.RefreshIconS {
    color: #fff;
    font-size: 24px;
    margin: 10px 0 0 2rem;
    cursor: pointer;
}









.HeadingS {

    font-family: AvertaStd-Semibold;
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 0.02em;
    margin-bottom: 25px;
    color: #000000;
}

.form_S {
    padding: 10px 30px 40px 30px;
    border: 2px solid #000000;
    box-sizing: border-box;
    border-radius: 8px;
    display: flex;
    align-items: center;
}

.form_S p {
    font-family: AvertaStd-Regular;
    font-size: 18px;
    padding-top: 22px;
    line-height: 23px;
    margin-bottom: 10px;
    margin-left: 10px;
    letter-spacing: 0.02em;
    color: #000000;
}

.form_K label {
    margin-left: 50px;
    font-family: AvertaStd-Semibold;
    font-size: 18px;
    line-height: 23px;
    letter-spacing: 0.02em;
    text-align: right;
}


.resetBtnS {
    position: relative;
    font-family: AvertaStd-Semibold;
    margin-top: 50px;
    margin-left: 12vh;
    font-size: 16px;
    border: 2px solid rgba(0, 0, 0, 0.7);
    box-sizing: border-box;
    filter: drop-shadow(0px 1px 0px rgba(0, 0, 0, 0.25));
    border-radius: 2px;
    width: 88px;
    height: 38px;
    line-height: 23px;
    letter-spacing: 0.02em;
    color: rgba(0, 0, 0, 0.7);
}

.saveBtnS {
    font-family: AvertaStd-Semibold;
    font-size: 16px;
    margin-left: 3vh;
    background: #fff;
    border: 2px solid #0F9D58;
    box-sizing: border-box;
    border-radius: 2px;
    filter: drop-shadow(0px 1px 0px rgba(0, 0, 0, 0.25));
    border-radius: 2px;
    width: 88px;
    height: 38px;
    line-height: 23px;
    letter-spacing: 0.02em;
    color: #0F9D58;
}

.GroupNameS {
    border: 2px solid rgba(0, 0, 0, 0.7);
    border-radius: 2px;
    padding: 10px;
    margin-left: 10px;
    margin-right: 4vh;
    font-size: 16px;
    height: 43px;
    width: 280px;
}
