.usergrouptable {
  font-family: arial, sans-serif;
  margin-left: 4em;

  border-collapse: collapse;
  margin-top: 4em;
  outline: none;
  width: 80.86%;
  outline: none;
  overflow-x: auto;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 0px 0px 6px 6px;
  /* margin-top: 20px; */
}

.tableOut {
  overflow-x: auto;
}

.usergrouptable th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
  font-family: AvertaStd-Semibold;
  font-size: 16px;
  line-height: 26px;
  text-align: center;
  letter-spacing: 0.02em;
  background: #4285f4;
  color: #fff;
  font-weight: 1;
}
.usergrouptable td {
  text-align: center;
  padding: 10px;
  border: 1.5px solid rgba(0, 0, 0, 0.646);
  text-align: center;
  font-family: Avertastd-Semibold;
  background: #fff;
  color: #000;
  font-weight: 1;
}
